<template>
  <div class="signup-success">
    <h2>Obrigado <br> {{user.fullName}}!</h2>
    <div class="signupMsg">Um email foi enviado. Clique no link para confirmar o seu cadastro</div>
    <br>
    <div class="subsMessage" v-if="checkSubscriptionPlan" >Também entraremos em contato com instruções para pagamento do plano selecionado.</div>

    <router-link class="loginlink" to="/login">Fazer login</router-link>
  </div>
</template>

<script>
  export default {
    name: "signup-sucess",
    props: ["user", "subscriptionPlan"],
    computed: {
      checkSubscriptionPlan() {
        if (this.subscriptionPlan != "free") {
          return true
        }else{
          return false
        }
      }
    },
  }
</script>

