<template>
  <div class="userConfig" v-if="user">
    <navigation-menu :title="'Configurações'" :arrowBack="arrowBack">
    </navigation-menu>

    <div class="userDetails" v-if="!passwordForm && !emailForm">

      <div class="detail fullname"><strong>Nome:</strong> {{user.fullName}}</div>
      <div class="detail email"><strong>Email:</strong> {{user.email}}</div>

      <div class="detail currentsubscriptionPlan">
        <strong>Plano:</strong> {{ subscriptionPlan }}
      </div>

      <div class="subscriptionPlans" v-if="subscriptionPlan == 'Grátis'">

        <strong>Planos disponíveis:</strong>

        <div class="subscriptionPlan">
          <div class="label">Mensal</div>
          <div class="is-2 pricing">R$ 29,90</div>
          <div class="getButton">
            <a class="" @click="subscribePlan('monthly')">Assinar</a>
          </div>
        </div>
        <div class="subscriptionPlan">
          <div class="label">Premium</div>
          <div class="pricing">R$ 599,00</div>
          <div class="getButton">
            <a class="" @click="subscribePlan('premium')">Assinar</a>
          </div>
        </div>
        <div class="compare">
          <a href="/#pricing" target="_blank">Compare os planos</a>
        </div>
      </div>

      <div class="has-text-centered">
        <a class="button is-primary changePassword" @click="changePassword()">Trocar Senha</a>
        <!-- <a class="button is-primary changeEmail" @click="changeEmail()">Trocar E-mail</a> -->
      </div>
    </div>

    <password-form v-if="passwordForm"></password-form>

    <email-form v-if="emailForm"></email-form>

  </div>
</template>

<script>
  import NavigationMenu from "../NavigationMenu";
  import PasswordForm from "./PasswordForm";
  import EmailForm from "./EmailForm.vue";

  export default {
    name: "user-config",
    components: {
      NavigationMenu,
      PasswordForm,
      EmailForm,
    },
    data() {
      return {
        arrowBack: true,
        user: null,
        passwordForm: false,
        emailForm: false,
      }
    },
    mounted () {
      this.user = this.$store.getters.getUser
    },
    methods: {
      subscribePlan(plan) {
        let req = {
          id: this.user.id,
          subscriptionPlan: plan
        }

        this.$store.dispatch('subscribePlan', req).then(()=>{
          this.$buefy.dialog.alert({
            title: 'Assinatura de plano',
            message: '<p>Em minutos entraremos em contato para prosseguir com a solicitação.</p><br><p>Agradecemos a preferência!</p>',
            confirmText: 'OK'
          })
        })
      },
      changePassword() {
        this.passwordForm = true
      },
      changeEmail(){
        this.emailForm = true
      }
    },
    computed: {
      subscriptionPlan() {
        if (this.user.subscriptionPlan != null && this.user.subscriptionPlan != "" && this.user.subscriptionPlan != "free"){
          return this.user.subscriptionPlan
        } else if(this.user.subscriptionPlan == "premium"){
          return "Premium"
        } else if(this.user.subscriptionPlan == "monthly"){
          return "Mensal"
        } else {
          return "Grátis"
        }
      }
    },
  }
</script>

