import axios from 'axios'

export default {
  post (plan) {
    return axios.post('/api/v2/plan', plan)
      .then((response) => Promise.resolve(response))
      .catch((error) => Promise.reject(error))
  },
  getAll (filter = '') {
    return axios.get('/api/v2/plans?' + filter)
      .then((response) => Promise.resolve(response))
      .catch((error) => Promise.reject(error))
  },
  get (id) {
    return axios.get('/api/v2/plan/' + id)
      .then((response) => Promise.resolve(response))
      .catch((error) => Promise.reject(error))
  },
  edit (plan) {
    return axios.put('/api/v2/plan/' + plan.id, plan)
      .then((response) => Promise.resolve(response))
      .catch((error) => Promise.reject(error))
  },
  delete (id) {
    return axios.delete('/api/v2/plan/' + id)
      .then((response) => Promise.resolve(response))
      .catch((error) => Promise.reject(error))
  }
}
