    getLoggedUser(){
      return this.$store.getters.getUser
    },
    isFreePlan(){
      if ( !this.$store.getters.getUser.subscriptionPlan || this.$store.getters.getUser.subscriptionPlan == 'free' || this.$store.getters.getUser.subscriptionPlan == ''){
        return true
      } else {
        return false
      }
    },
    showDrawTools(){
      if (this.$route.name == "Plan"){
        return true
      } 
      if (this.$route.name == "Project"){
        return true
      } 
      return false
    },
    showEditTool(){
      if (this.$route.name == "Annotation"){
        return true
      } 
      return false
    },
    computedOrthomosaics(){
      return this.orthomosaics
      // return this.$store.getters.getOrthomosaics
    },
    computedAnnotations(){
      // return this.annotations
      return this.annotations
    },
    getPlanGeometry(){
      if (this.$store.getters.getCurrentPlan != null && this.$store.getters.getCurrentPlan.geometrys.length > 0){
        var teste = this.$store.getters.getCurrentPlan.geometrys
        let result = teste.map(a => [a.latitude, a.longitude]);
  
        return result
      }else{
        return null
      }
    },
  },
