    LPolyline,
    LControlLayers,
    LFeatureGroup,
    LPolygon,
    "draw": Draw,
    "v-polygon": Polygon,
    "v-polyline": Polyline,
    "v-rectangle": Rectangle,
    "v-circle": Circle,
    "v-marker": Marker
