import axios from 'axios'

export default {

  login (user) {
    return axios.post('/api/v2/login', user)
      .then((response) => Promise.resolve(response))
      .catch((error) => Promise.reject(error))
  },
  checktoken (token) {
    return axios.post('/api/v2/checktoken', token)
      .then((response) => Promise.resolve(response))
      .catch((error) => Promise.reject(error))
  },
  logout () {
    return axios.post('/api/v2/logout')
      .then((response) => Promise.resolve(response))
      .catch((error) => Promise.reject(error))
  },
  register (user) {
    return axios.post('/api/v2/user', user)
      .then((response) => Promise.resolve(response))
      .catch((error) => Promise.reject(error))
  },
  facebookLogin (facebookUser) {
    return axios.post('/api/v2/facebooklogin', facebookUser)
      .then((response) => Promise.resolve(response))
      .catch((error) => Promise.reject(error))
  },
}
