import {services} from './../api'

const state = {
  projects: [],
  currentProject: null,
  newProject: null
}

const getters = {
  getProjects: (state, getters, rootState, rootGetters) => {
    return state.projects.filter(project => {
      return project.name.toLowerCase().includes(rootGetters.getSearchTerm.toLowerCase())
    })
    // state.projects,
  },
  getCountProjects: (state, getters, rootState, rootGetters) => getters.getProjects.length,
  // getCountProjects: state => state.projects.length,
  getCurrentProject: state => state.currentProject,
  getNewProject: state => state.newProject
}

const mutations = {
  set_newproject (state, project) {
    state.newProject = project
  },  
  post_project (state, project) { // Ajustar
    state.projects.push(project)
  },  
  get_projects (state, projects) {
    state.projects = projects
  },
  get_project (state, project) {
    state.currentProject = project
  },
  edit_project (state, currentProject) {
    var index = state.projects.findIndex(project => project.id === currentProject.id)
    state.projects[index] = currentProject
    // state.projects.splice(index, 1)
    // state.projects.push(project)
  },
  delete_project(state, projectId) {
    var index = state.projects.findIndex(project => project.id === projectId)
    state.projects.splice(index, 1)
  }
}

const actions = {
  setNewProject ({
    commit
  }, project) {
    return new Promise((resolve, reject) => {
      commit('set_newproject', this._vm.$clone(project))
      resolve(project)
    })
  },
  postProject ({
    commit
  }, project) {
    return services.projects.post(project)
      .then((response) => {
        commit('post_project', this._vm.$clone(response.data))
        commit('set_newproject', this._vm.$clone(response.data))
        return Promise.resolve(response.data)
      })
      .catch((error) => Promise.reject(error))
  },
  getProjects ({
    commit
  }, filter) {
    return services.projects.getAll(filter)
    .then((response) => {
      commit('get_projects', this._vm.$clone(response.data))
      return Promise.resolve(response.data)
    })
    .catch((error) => Promise.reject(error))
  },
  getProject ({
    commit
  }, id) {
    return services.projects.get(id)
    .then((response) => {
      commit('get_project', this._vm.$clone(response.data))
      return Promise.resolve(response.data)
    })
    .catch((error) => Promise.reject(error))
  },
  editProject ({
    commit
  }, project) {
    return services.projects.edit(project)
      .then((response) => {
        commit('edit_project', this._vm.$clone(response.data))
        return Promise.resolve(response.data)
      })
      .catch((error) => Promise.reject(error))
  },
  deleteProject({
    commit
  }, id) {
    return services.projects.delete(id)
      .then((response) => {
        commit('delete_project', id)
        return Promise.resolve(response.data)
      })
      .catch((error) => Promise.reject(error))
  }
}

export default {
  state,
  getters,
  mutations,
  actions
}
