<template>
  <div class="annotation">
    <b-loading :is-full-page="false" :active.sync="isLoading" :can-cancel="false"></b-loading>

    <navigation-menu v-if="annotation && !isLoading" :title="annotation.name" :arrowBack="arrowBack">
      <div class="editButton button" v-on:click.stop="editAnnotation()">
        <i class="fas fa-edit"></i>
      </div>
      <div class="deleteButton button" v-on:click.stop="deleteAnnotation()">
        <i class="fas fa-trash"></i>
      </div>
    </navigation-menu>

    <annotation-details v-if="annotation && !isLoading && !edit" :annotation="annotation"></annotation-details>
    <annotation-form v-if="annotation && !isLoading && edit" :annotationObj="annotation"></annotation-form>
    
    <div class="errorNotFound" v-if="error && !isLoading">
      Desculpe, esta anotação não foi encontrada.<br /> :(
    </div>
  </div>
</template>

<script>
  import NavigationMenu from '../NavigationMenu'
  import AnnotationDetails from '../annotation/AnnotationDetails'
  import AnnotationForm from '../annotation/AnnotationForm'

  export default {
    name: "annotation",
    components: {
      NavigationMenu,
      "annotation-details": AnnotationDetails,
      "annotation-form": AnnotationForm,
    },
    props: {
      annotationObj: {
        type: Object,
        default: null
      },
      annotationId: {
        type: String,
        default: null
      },
    },
    data() {
      return {
        annotation: null,
        isLoading: false,
        edit: false,
        error: false,
        arrowBack: true
      }
    },
    beforeRouteUpdate (to, from, next) {
      this.getAnnotation(to.params.annotationId)
      next();
    },
    mounted () {
      this.getAnnotation(this.annotationId)
      this.$root.$on('edit-annotation', (annotation)=>{
        this.updateAnnotation(annotation)
      })
      
    },

    methods: {

      updateAnnotation(annotation){
        if (this.annotation.id == annotation.id) {
          this.annotation = annotation
          this.edit = false
        }
      },

      getCentroid(arr) { 
        return arr.reduce(function (x,y) {
          return [x[0] + y[0]/arr.length, x[1] + y[1]/arr.length] 
        }, [0,0]) 
      },

      centerAnnotation(annotation) {
        let center
        let centerObj
        if (annotation.type == "rectangle" || annotation.type == "polygon"){
          center = this.getCentroid(annotation.latlngs)
        }
        if (annotation.type == "circle" || annotation.type == "marker"){
          center = annotation.latlng
        }
        if (annotation.type == "polyline"){
          // melhorar o centro da polyline, atualmente pega o primeiro ponto
          center = annotation.latlngs[0]
        }

        centerObj = {
          latitude: center[0],
          longitude: center[1]
        }

        this.$root.$emit('centerMap', centerObj)
      },

      async getAnnotation(id) {
        this.isLoading = true
        this.$store.dispatch('getAnnotation', id)
          .then( (annotation) => {
            this.annotation = annotation
            this.centerAnnotation(annotation)
            
            this.isLoading = false
          })
          .catch((error)=>{
            this.isLoading = false
            this.error = true
          })
      },

      editAnnotation(){
        this.edit = true
        // this.$router.push({name: 'EditAnnotation', params: {annotationObj: this.annotation}})
      },

      deleteAnnotation() {
        this.$root.$emit('delete-annotation', this.annotation.id)
        this.$store.dispatch('deleteAnnotation', this.annotation.id)
        
        this.$store.dispatch('deleteAnnotation', this.annotation.id)
        .then( () => {
          this.$router.go(-1)
          this.$notify({
            group: 'global',
            type: 'is-success',
            duration: 2000,
            title: 'Anotação removido'
          });
        })
      },
    },
  }
</script>

<style lang="scss" scoped>
.annotation{
  width: 100%;
  padding: 10px;
  overflow-x: hidden;
  overflow-y: auto;

  .navigationMenu{
    margin-bottom: 20px;

    .editButton{
      color: $color-darker;
    }
    .deleteButton{
      color: $color-darker;
    }
  }
}
</style>