<template>
  <div class="plan">
    <b-loading :is-full-page="false" :active.sync="isLoading" :can-cancel="false"></b-loading>

    <navigation-menu v-if="plan && !isLoading" :title="plan.name" :arrowBack="arrowBack && !compareMosaics">
      <div class="editButton button" v-on:click.stop="editMode()">
        <i class="fas fa-edit"></i>
      </div>
      <div class="deleteButton button" v-on:click.stop="confirmCustomDelete()" v-if="!example && arrowBack">
        <i class="fas fa-trash"></i>
      </div>
    </navigation-menu>

    <compare-mosaics :orthomosaics="plan.orthomosaics" v-if="compareMosaics"></compare-mosaics>

    <plan-details v-if="plan && !isLoading && !edit && !compareMosaics" :plan="plan"></plan-details>

    <b-button class="selectMosaicsToCompare is-primary" @click="selectMosaicsToCompare()" v-if="!compareMosaics && !isLoading">
      <i class="fas fa-arrows-alt-h"></i>
      Comparar Ortomosaicos
    </b-button>

    <div class="editPlan" v-if="edit">
      <plan-form v-if="plan && !isLoading" :planObj="plan" :showButton="false" ref="planForm"></plan-form>

      <b-field class="is-pulled-right">
        <b-button
          class="button next-bt is-primary"
          @click="updatePlan"
          :loading="inputLoading"
          :disabled="inputLoading"
        >Salvar</b-button>
      </b-field>
      <b-field class="is-pulled-left">
        <b-button
          class="button next-bt is-primary"
          @click="cancelEditMode"
        >Cancelar</b-button>
      </b-field>
    </div>

    <div class="errorNotFound" v-if="error && !isLoading">
      Desculpe, este mapa não foi encontrado.<br /> :(
    </div>
  </div>
</template>

<script>
  import NavigationMenu from "../NavigationMenu";
  import PlanDetails from  './PlanDetails.vue'
  import PlanForm from  './PlanForm.vue'
  import CompareMosaics from './CompareMosaics'

  export default {
    name: "plan",
    components:{
      NavigationMenu,
      "plan-details": PlanDetails,
      "plan-form": PlanForm,
      CompareMosaics
    },
    props: {
      planId: {
        type: String
      },
      share: {
        type: Object
      },
      example: {
        type: Boolean,
        default: false
      },
    },
    data() {
      return {
        plan: null,
        edit: false,
        inputLoading: false,
        error: false,
        isLoading: false,
        arrowBack: true,
        compareMosaics: false
      }
    },

    // beforeDestroy() {
    //   this.$root.$emit('clear-orthomosaics')
    // },

    mounted () {
      this.isLoading = true
      if(this.share && this.share.linkSharing){
        this.arrowBack = false
      }
      this.$store.dispatch('getPlan', this.planId)
      .then( async (plan) => {
        this.plan = plan
        
        // this.plan.name = plan.name
        // this.plan.tasks = plan.tasks
        
        this.isLoading = false

      })
      .catch((e)=>{
        console.log(e)
        this.isLoading = false
        this.error = true
      })
          
    },

    methods: {
      selectMosaicsToCompare(){
        this.compareMosaics = true
      },
      editMode(){
        this.edit = true
      },
      cancelEditMode() {
        this.edit = false;
      },
      centerMap(coords) {
        this.$root.$emit('centerMap', coords)
      },
      updatePlan(){
        this.inputLoading = true
        this.$refs.planForm.submit().then(async (plan)=>{
          this.$store.dispatch('setNewPlan', plan)
          this.plan = plan

          this.inputLoading = false
          this.edit = false
        }).catch(()=>{
          this.inputLoading = false
        })
      },
      isActive(ref){
        var index = this.$store.getters.getActiveOrthomosaics.findIndex(orthomosaic => orthomosaic.id === ref.id)

        if (index != -1){
          return true
        }
        return false

      },
      deleteItem() {
        if(!this.share){
          this.$store.dispatch('deletePlan', this.planId)
          .then( () => {
            this.$notify({
              group: 'global',
              type: 'is-success',
              duration: 2000,
              title: 'Mapa removido'
            });
            this.$router.go(-1)
          })
        }
        else {
          this.$store.dispatch('deleteSharing', this.share.id)
          .then( () => {
            this.$notify({
              group: 'global',
              type: 'is-success',
              duration: 2000,
              title: 'Projeto compartilhado removido'
            });
            this.$router.go(-1)
          })
        }        
      },

      confirmCustomDelete() {
        this.$buefy.dialog.confirm({
          title: 'Deletando Mapa',
          message: 'Você tem certeza que deseja <b>deletar</b> o mapa ? Isso é uma ação sem volta.',          
          confirmText: 'Deletar Mapa',
          type: 'is-danger',
          hasIcon: true,
          onConfirm: () => this.deleteItem()
        })
      },
    },
  }
</script>

<style lang="scss" scope>
  .plan{
    width: 100%;
    padding: 10px;
    overflow-x: hidden;
    overflow-y: auto;

    &::-webkit-scrollbar-track {
      background-color: #f5f5f5;
    }

    &::-webkit-scrollbar {
      width: 8px;
      background-color: #f5f5f5;
    }

    &::-webkit-scrollbar-thumb {
      // border-radius: 10px;
      background-color: #888;
    }

    .selectMosaicsToCompare{
      width: 100%;
      margin-bottom: 20px;
    }

    .errorNotFound{
      text-align: center;
      font-weight: bold;
      margin-top: 20px;
    }
  }

</style>