import axios from 'axios'

export default {

  post (task) {
    return axios.post('/api/v2/task', task)
      .then((response) => Promise.resolve(response))
      .catch((error) => Promise.reject(error))
  },

  run (task) {
    return axios.get('/api/v2/task/' + task.id + '/run')
      .then((response) => Promise.resolve(response))
      .catch((error) => Promise.reject(error))
  },
  getAll (filter = '') {
    return axios.get('/api/v2/task?' + filter)
      .then((response) => Promise.resolve(response))
      .catch((error) => Promise.reject(error))
  },

  get (id) {
    return axios.get('/api/v2/task/' + id)
      .then((response) => Promise.resolve(response))
      .catch((error) => Promise.reject(error))
  },

  edit (user) {
    return axios.put('/api/v2/task/' + user.id, user)
      .then((response) => Promise.resolve(response))
      .catch((error) => Promise.reject(error))
  },

  delete (id) {
    return axios.delete('/api/v2/task/' + id)
      .then((response) => Promise.resolve(response))
      .catch((error) => Promise.reject(error))
  }

}
