import axios from 'axios'

export default {

  post (sharing) {
    return axios.post('/api/v2/sharing', sharing)
      .then((response) => Promise.resolve(response))
      .catch((error) => Promise.reject(error))
  },
  getAll (filter = '') {
    return axios.get('/api/v2/sharing?' + filter)
      .then((response) => Promise.resolve(response))
      .catch((error) => Promise.reject(error))
  },

  get (id) {
    return axios.get('/api/v2/sharing/' + id)
      .then((response) => Promise.resolve(response))
      .catch((error) => Promise.reject(error))
  },

  edit (sharing) {
    return axios.put('/api/v2/sharing/:id' + sharing.id, sharing)
      .then((response) => Promise.resolve(response))
      .catch((error) => Promise.reject(error))
  },

  delete (id) {
    return axios.delete('/api/v2/sharing/' + id)
      .then((response) => Promise.resolve(response))
      .catch((error) => Promise.reject(error))
  }

}
