import axios from 'axios'

export default {
  post (geometry) {
    return axios.post('/api/v2/geometry', geometry)
      .then((response) => Promise.resolve(response))
      .catch((error) => Promise.reject(error))
  },
  getAll (filter = '') {
    return axios.get('/api/v2/geometrys?' + filter)
      .then((response) => Promise.resolve(response))
      .catch((error) => Promise.reject(error))
  },
  get (id) {
    return axios.get('/api/v2/geometry/' + id)
      .then((response) => Promise.resolve(response))
      .catch((error) => Promise.reject(error))
  },
  edit (geometry) {
    return axios.put('/api/v2/geometry/' + geometry.id, geometry)
      .then((response) => Promise.resolve(response))
      .catch((error) => Promise.reject(error))
  },
  delete (id) {
    return axios.delete('/api/v2/geometry/' + id)
      .then((response) => Promise.resolve(response))
      .catch((error) => Promise.reject(error))
  }
}
