<template>
  <div class="project">
    <b-loading :is-full-page="false" :active.sync="isLoading" :can-cancel="false"></b-loading>

    <navigation-menu v-if="project && !isLoading" :title="project.name" :arrowBack="arrowBack && !comparePlans">
      <div class="editButton button" v-on:click.stop="editMode()">
        <i class="fas fa-edit"></i>
      </div>
      <div class="deleteButton button" v-on:click.stop="confirmCustomDelete()" v-if="!example && arrowBack">
        <i class="fas fa-trash"></i>
      </div>
    </navigation-menu>

    <b-button class="selectPlansToCompare is-primary" @click="selectPlansToCompare()" v-if="!comparePlans && !isLoading">
      <i class="fas fa-arrows-alt-h"></i>
      Comparar Mapas
    </b-button>

    <project-details v-if="project && !isLoading && !edit && !comparePlans" :project="project"></project-details>

    <compare-plans :plans="project.plans" v-if="comparePlans"></compare-plans>

    <div class="editProject" v-if="edit">
      <project-form v-if="project && !isLoading" :projectObj="project" :showButton="false" ref="projectForm"></project-form>

      <b-field label="Mapas">
      </b-field>
      <b-field>
        <b-select v-model="currentPlan" placeholder="Selecione" expanded>
          <option
            v-for="plan in filteredPlans"
            :value="plan.id"
            :key="plan.id">
            {{ plan.name }}
          </option>
        </b-select>

        <b-button class="selectPlan is-primary" @click="addPlan()">
          <i class="fas fa-plus-circle"></i>
          Selecionar
        </b-button>
      </b-field>

      <b-field label="Mapas selecionados:" v-if="project.plans && project.plans.length">
        <ul class="selectedMaps">
          <li class="selectedMap"
            v-for="plan in project.plans"
              :value="plan.id"
              :key="plan.id">
              {{ plan.name }}

            <div class="remove" @click="removePlan(plan.id)">
              <i class="fas fa-times"></i>
            </div>
          </li>
        </ul>
      </b-field>


      <b-field class="is-pulled-right">
        <b-button
          class="button next-bt is-primary"
          @click="updateProject"
          :loading="inputLoading"
          :disabled="inputLoading"
        >Salvar</b-button>
      </b-field>
      <b-field class="is-pulled-left">
        <b-button
          class="button next-bt is-primary"
          @click="cancelEditMode"
        >Cancelar</b-button>
      </b-field>
    </div>
      
    <div class="errorNotFound" v-if="error && !isLoading">
      Desculpe, este projeto não foi encontrado.<br /> :(
    </div>
  </div>
</template>

<script>
  import NavigationMenu from '../NavigationMenu'
  import ProjectDetails from './ProjectDetails'
  import ProjectForm from './ProjectForm'
  import ComparePlans from './ComparePlans'

  export default {
    name: 'project',
    components: {
      NavigationMenu,
      "project-details": ProjectDetails,
      "project-form": ProjectForm,
      ComparePlans
    },
    props: {
      projectId: {
        type: String
      },
      share: {
        type: Object,
        default: null
      },
      example: {
        type: Boolean,
        default: false
      },
    },
    data() {
      return {
        project: { },
        addClicked: false,
        edit: false,
        error: false,
        isLoading: false,
        inputLoading: false,
        listPlans: [],
        currentPlan: {},
        arrowBack: true,
        comparePlans: false
      }
    },
    computed: {
      filteredPlans() {
        if (this.project.plans){
          let self = this

          return this.listPlans.filter(function (el) {
            return !self.project.plans.some(function (f) {
              return f._id === el._id;
            });
          });

        }else{
          return this.listPlans
        }
      }
    },
    mounted() {
      this.$store.dispatch('getPlans', "owner="+this.$store.getters.getUser.id)
      .then((plans) => {
        this.listPlans = plans
      })

      this.isLoading = true
      if(this.share && this.share.linkSharing){
        this.arrowBack = false
      }

      this.$store.dispatch('getProject', this.projectId)
      .then( (project) => {

        this.project = project
        this.isLoading = false

      })
      .catch(()=>{
        this.isLoading = false
        this.error = true
      })
    },
    methods: {
      selectPlansToCompare(){
        this.comparePlans = true
      },
      editMode(){
        this.edit = true
      },
      cancelEditMode() {
        this.edit = false;
      },
      updateProject(){
        this.inputLoading = true
        this.$refs.projectForm.submit().then(async (project)=>{
          this.$store.dispatch('setNewProject', project)
          this.project = project

          this.inputLoading = false
          this.edit = false
        }).catch(()=>{
          this.inputLoading = false
        })
      },

      async addPlan () {
        var index = this.listPlans.findIndex(plan => plan.id === this.currentPlan)
        this.project.plans.push(this.listPlans[index])

        await this.$store.dispatch('editProject', this.project)

        this.currentPlan = {}
        this.listPlans.splice(index, 1)
      },

      async removePlan (planId) {
        var index = this.project.plans.findIndex(plan => plan.id === planId)
        this.project.plans.splice(index, 1)

        await this.$store.dispatch('editProject', this.project)
      },

      centerMap(coords) {
        this.$root.$emit('centerMap', coords)
      },

      deleteItem() {
        if(!this.share){
          this.$store.dispatch('deleteProject', this.projectId)
          .then( () => {
            // this.$parent.removeItem(this.object)
            this.$notify({
              group: 'global',
              type: 'is-success',
              duration: 2000,
              title: 'Projeto removido'
            });
            this.$router.go(-1)
          })
        }
        else{
          this.$store.dispatch('deleteSharing', this.share.id)
          .then( () => {
            this.$notify({
              group: 'global',
              type: 'is-success',
              duration: 2000,
              title: 'Projeto compartilhado removido'
            });
            this.$router.go(-1)
          })
        }        
      },

      confirmCustomDelete() {
        this.$buefy.dialog.confirm({
          title: 'Deletando Projeto',
          message: 'Você tem certeza que deseja <b>deletar</b> o projeto ? Isso é uma ação sem volta.',          
          confirmText: 'Deletar Projeto',
          type: 'is-danger',
          hasIcon: true,
          onConfirm: () => this.deleteItem()
        })
      },



    },
  }
</script>

<style lang="scss" scoped>

.project{
  width: 100%;
  padding: 10px;
  overflow-y: auto;

  &::-webkit-scrollbar-track {
    background-color: #f5f5f5;
  }

  &::-webkit-scrollbar {
    width: 8px;
    background-color: #f5f5f5;
  }

  &::-webkit-scrollbar-thumb {
    // border-radius: 10px;
    background-color: #888;
  }

  .navigationMenu{
    margin-bottom: 20px;
    margin-top: 20px;
  }

  .selectPlansToCompare{
    width: 100%;
    margin-top: 10px;
    margin-bottom: 20px;
  }

  .errorNotFound{
    text-align: center;
    font-weight: bold;
    margin-top: 20px;
  }


  .createPlan{
    cursor: pointer;
  }

  .selectPlan{
    cursor: pointer;
  }

  .step-items{
    width: 100%;
  }

  .selectedMaps{

    .selectedMap{
      position: relative;
      padding: 5px;
      background-color: $color-lighter;
      color: $color-dark;
      margin-bottom: 5px;

      .remove{
        color: red;
        cursor: pointer;
        @include v-centerer;
        right: 10px;
      }
    }
  }
  
}

</style>