<script>
import FormEmailConfirmation from "@/components/EmailConfirmation.form";

export default {
  name: "email-confirmation-view",
  data: () => ({
    email: "",
    expired: false,
    used: false
  }),

  components: {
    'email-confirmation': FormEmailConfirmation
  },

  mounted() {
    if ('expired' in this.$route.query){
      this.expired = true
      this.$buefy.toast.open({
        message: "Token expirado, tente novamente",
        type: "is-danger",
        position: "is-bottom"
      });
    }

    if ('used' in this.$route.query){
      this.used = true
      this.$buefy.toast.open({
        message: "Este email já foi está confirmado",
        type: "is-danger",
        position: "is-bottom"
      });
    }
  },

  methods: {
  }
};
</script>

