import axios from 'axios'

export default {
  post (email) {
    return axios.post('/api/v2/email', email)
      .then((response) => Promise.resolve(response))
      .catch((error) => Promise.reject(error))
  },
  postTask(params) {
    // console.log(params)
    return axios.post('/api/v2/deliver/contactFormVantumTask', params)
      .then((response) => Promise.resolve(response))
      .catch((error) => Promise.reject(error))
  },
  getAll (filter = '') {
    return axios.get('/api/v2/emails?' + filter)
      .then((response) => Promise.resolve(response))
      .catch((error) => Promise.reject(error))
  },
  get (id) {
    return axios.get('/api/v2/email/' + id)
      .then((response) => Promise.resolve(response))
      .catch((error) => Promise.reject(error))
  },
  edit (email) {
    return axios.put('/api/v2/email/' + email.id, email)
      .then((response) => Promise.resolve(response))
      .catch((error) => Promise.reject(error))
  },
  delete (id) {
    return axios.delete('/api/v2/email/' + id)
      .then((response) => Promise.resolve(response))
      .catch((error) => Promise.reject(error))
  },
  postIdea (idea) {
    return axios.post('/api/v2/deliver/contactcampaign', idea)
      .then((response) => Promise.resolve(response))
      .catch((error) => Promise.reject(error))
  }
}
