import axios from 'axios'

export default {

  post (invoice) {
    return axios.post('/api/v2/invoice', invoice)
      .then((response) => Promise.resolve(response))
      .catch((error) => Promise.reject(error))
  },
  getAll (filter = '') {
    return axios.get('/api/v2/invoices?' + filter)
      .then((response) => Promise.resolve(response))
      .catch((error) => Promise.reject(error))
  },

  get (id) {
    return axios.get('/api/v2/invoice/' + id)
      .then((response) => Promise.resolve(response))
      .catch((error) => Promise.reject(error))
  },

  edit (invoice) {
    return axios.put('/api/v2/invoice/' + invoice.id, invoice)
      .then((response) => Promise.resolve(response))
      .catch((error) => Promise.reject(error))
  },

  delete (id) {
    return axios.delete('/api/v2/invoice/' + id)
      .then((response) => Promise.resolve(response))
      .catch((error) => Promise.reject(error))
  },

  addCoupon (params) {
    return axios.post('/api/v2/invoice/addcoupon', params)
      .then((response) => Promise.resolve(response))
      .catch((error) => Promise.reject(error))
  },

  removeCoupon (params) {
    return axios.post('/api/v2/invoice/removecoupon', params)
      .then((response) => Promise.resolve(response))
      .catch((error) => Promise.reject(error))
  }

}
