<template>
  <div class="signup-form">
    <form>
      <b-field 
        label="Nome"
        :type="{'is-danger': errors.has('name')}"
        :message="errors.first('name')">
        <b-input v-model="name"
          v-validate="'required'"
          name="name"
          icon-pack="fas"
          icon="user"></b-input>
      </b-field>

      <b-field
        label="Email"
        :type="{'is-danger': errors.has('email')}"
        :message="errors.first('email')">
        <b-input type="text"
          v-model="email"
          name="email"
          v-validate="'required|email|verify_email'"
          icon-pack="fas"
          icon="envelope"/>
      </b-field>

      <b-field
        label="Telefone"
        :type="{'is-danger': errors.has('phone')}"
        :message="errors.first('phone')">

        <div class="phone-field">
          <vue-country-code
            class="phone-country"
            @onSelect="onSelectCountry"
            :disabledFetchingCountry="true"
            :preferredCountries="['br']"
            :enabledCountryCode="true" />
                    
          <b-input type="tel"
            class="phone-input"
            v-model="phone.number"
            v-mask="'(##) ### ### ###'"
            name="phone"
            v-validate="'required'" />
        </div>

      </b-field>

      <b-field label="Senha"
        :type="{'is-danger': errors.has('password')}"
        :message="errors.first('password')">
        <b-input type="password"
          value=""
          name="password"
          v-validate="'required|min:8|verify_password'"
          ref="password"
          password-reveal
          v-model="password"
          icon-pack="fas"
          icon="key">
        </b-input>
      </b-field>

      <b-field label="Confirme sua senha"
        :type="{'is-danger': errors.has('password_confirmation')}"
        :message="errors.first('password_confirmation')">
        <b-input type="password"
          value=""
          v-validate="'required|confirmed:password'"
          name="password_confirmation"
          password-reveal
          v-model="password_confirmation"
          icon-pack="fas"
          icon="key">
        </b-input>
      </b-field>
      
      <b-field
        :type="{'is-danger': errors.has('flag-terms')}"
        :message="{'Você deve aceiar os termos para efetuar o cadastro' : errors.firstByRule('flag-terms', 'required')}">
        <b-checkbox 
          v-model="flagTerms" 
          name="flag-terms" 
          v-validate="'required:true'"
          type="is-success">
            Declaro que li e aceito&nbsp;<span class="terms-link" @click.stop="isTermsModalActive = true">os termos e condições</span>.
        </b-checkbox>
      </b-field>

      <b-field>
        <b-button class="signup-button button" :loading="loading" @click="validateBeforeSubmit">Registrar</b-button>
        <!-- <a class="signup-button button" @click="validateBeforeSubmit">Registrar</a> -->
      </b-field>
    </form>

    <b-modal :active.sync="isTermsModalActive" has-modal-card>
        <terms></terms>
    </b-modal>
  </div>
</template>

<script>
import Terms from '@/components/Terms'
import VueCountryCode from "vue-country-code";
import "vue-country-code/dist/vue-country-code.css";

export default {
  name: "signup-form",

  props: ["nextUrl"],
  components: {
    Terms,
    VueCountryCode
  },
  data() {
    return {
      isTermsModalActive: false,
      name: "",
      email: "",
      phone: {
        number: "",
        phoneType: "",
        countryCode: "55"
      },
      address: {
        address1: "",
        address2: "",
        houseNumber: "",
        zipCode: "",
        city: "",
        state: "",
        country: "brazil",
      },
      phoneType: "",
      countryCode: "55",
      password: "",
      password_confirmation: "",
      flagTerms: false,
      subscriptionPlan: "free",
      loading: false
      // is_admin: null
    };
  },
  mounted () {
    if (this.$route.query.name){
      this.name = this.$route.query.name;
    }
    if (this.$route.query.email){
      this.email = this.$route.query.email;
    }
    if (this.$route.query.subscriptionPlan){
      this.subscriptionPlan = this.$route.query.subscriptionPlan;
    }
  },
  methods: {
    onSelectCountry: function ({dialCode}) {
      this.phone.countryCode = dialCode
    },
    
    register: function () {
      this.loading = true
      let user = {
        name: this.name,
        email: this.email,
        phone: {
          countryCode: this.phone.countryCode,
          number: this.phone.number.replace(/\D/g,''),
          phoneType: this.phone.phoneType
        },
        password: this.password,
        subscriptionPlan: this.subscriptionPlan
      }
      this.$store.dispatch('register', user)
      .then((response) => {

        this.$router.push({name: 'SignupSuccess', params: {user: response.user, subscriptionPlan: this.subscriptionPlan}})
        this.loading = false

        // this.$parent.user = user.data.user
        // this.$parent.registered = true;
      })
      .catch((err) => {
        console.log(err)
        if (err.message == "User already registered"){
          this.$buefy.toast.open({
            message: 'Usuário já registrado. Esqueceu a senha?',
            type: 'is-danger',
            position: 'is-bottom'
          })
        }
      })
    },

    validateBeforeSubmit () {
      this.$validator.validateAll().then((result) => {
        if (result) {
          this.register()
          return
        }
        this.$buefy.toast.open({
          message: 'Formulário inválido, verifique os campos.',
          type: 'is-danger',
          position: 'is-bottom'
        })
      })
    }
  }
};
</script>

