import axios from 'axios'

export default {

  post (project) {
    return axios.post('/api/v2/project', project)
      .then((response) => Promise.resolve(response))
      .catch((error) => Promise.reject(error))
  },
  getAll (filter = '') {
    return axios.get('/api/v2/projects?' + filter)
      .then((response) => Promise.resolve(response))
      .catch((error) => Promise.reject(error))
  },

  get (id) {
    return axios.get('/api/v2/project/' + id)
      .then((response) => Promise.resolve(response))
      .catch((error) => Promise.reject(error))
  },
  getOwner (id) {
    return axios.get('/api/v2/projects?owner=' + id)
      .then((response) => Promise.resolve(response))
      .catch((error) => Promise.reject(error))
  },

  edit (project) {
    return axios.put('/api/v2/project/' + project.id, project)
      .then((response) => Promise.resolve(response))
      .catch((error) => Promise.reject(error))
  },

  delete (id) {
    return axios.delete('/api/v2/project/' + id)
      .then((response) => Promise.resolve(response))
      .catch((error) => Promise.reject(error))
  }

}
