<template>
  <div class="new-plan">
    <navigation-menu title="Novo Mapa"></navigation-menu>

    <b-steps size="is-small" v-model="activeStep" :animated="true" :has-navigation="false">
      <b-step-item type="is-success" icon-pack="fas" icon="map" :clickable="false">

        <plan-form :showButton="false" ref="planForm"></plan-form>

        <b-field>
          <b-button
            class="button next-bt is-primary is-pulled-right"
            @click="savePlan"
          >Próximo</b-button>
        </b-field>
      </b-step-item>

      <b-step-item type="is-success" icon-pack="fas" icon="images" :clickable="false">
        <b-field label="Tipo de Imagem" :type="{'is-danger': errors.has('ProcessType')}" :message="errors.first('ProcessType')">

        </b-field>
        <b-field v-if="showInputSelector">
          <b-radio v-model="inputType"
            size="is-small"
            type="is-success"
            :disabled="processing"
            native-value="album">
            Imagens de drone
          </b-radio>
          
          <b-radio v-model="inputType"
            size="is-small"
            :disabled="processing"
            type="is-success"
            native-value="orthomosaic">
            Ortomosaico
          </b-radio>
        </b-field>

        <album-form v-if="inputType == 'album'" :showButton="false" ref="albumForm"></album-form>
        <orthomosaic-form v-if="inputType == 'orthomosaic'" :showButton="false" ref="orthomosaicForm"></orthomosaic-form>


        <b-field>
          <b-button
            class="button prev-bt is-primary is-pulled-left"
            @click="previousStep"
          >Anterior</b-button>
        </b-field>
        <b-field>
          <b-button
            class="button next-bt is-primary is-pulled-right"
            @click="saveInputMethod"
            :loading="inputLoading"
            :disabled="inputLoading"
          >Próximo</b-button>
        </b-field>

      </b-step-item>

      <b-step-item type="is-success" icon-pack="fas" icon="credit-card"  :clickable="false" >
        <!-- Seu upload está em andamento e pode ser visualizado no botão amarelo da barra lateral. Por favor, feche ou não atualize a página.  -->
        <!-- Enquanto isso, você pode navegar livremente pela plataforma. -->
        <billing-view
        :album="album"
        :plan="plan"
        :task="task"
        :invoice="invoice"
        ref="billingView"
        :showButton="false">
        </billing-view>

        <b-field>
          <b-button
            class="button next-bt is-primary is-pulled-right"
            @click="finishNewPlan"
          >Finalizar</b-button>
        </b-field>
      </b-step-item>
    </b-steps>


  </div>
</template>

<script>
import NavigationMenu from "../NavigationMenu";
import PlanForm from '@/components/app/plan/PlanForm'
import AlbumForm from '@/components/app/album/AlbumForm'
import OrthomosaicForm from '@/components/app/orthomosaic/OrthomosaicForm'
import BillingView from '@/components/app/billing/BillingView'

export default {
  name: "new-plan",
  components: {
    NavigationMenu,
    PlanForm,
    AlbumForm,
    OrthomosaicForm,
    BillingView
  },
  data() {
    return {
      inputType: "",
      inputLoading: false,
      showInputSelector: true,
      processing: false,
      activeStep: 0,
      btDisabled: false,
      item: "",
      plan: {
        id: '',
        name: '',
        location: '',
        latlng: null,
        album: '',
        owner: this.$store.state.auth.user.id,
        project: null,
      },
      album: null,
      task: {
        id: null,
        job: {orthomosaic: true},
        status: "waiting"
      },
      invoice: null,
    };
  },
  mounted() {

    if (this.plan.album != null || this.plan.album.id != null){
      this.showInputSelector == false
      this.inputType == "album"
    }

    if (this.plan.orthomosaics != null || this.plan.orthomosaics != []){
      this.showInputSelector == false
      this.inputType == "orthomosaic"
    }
  },
  methods: {
    sleep (m) {return new Promise(r => setTimeout(r, m))},
    createTask: function() {
      return new Promise((resolve, reject) => {
        this.$store.dispatch('postTask', this.task)
        .then((task) => {
          this.task = task
          resolve(task)
        }).catch((e)=>{
          reject(e)
        })
      })
    },
    createInvoice: function(task) {
      return new Promise((resolve, reject) => {

        this.$store.dispatch('postInvoice', {
          task:task,
          owner: this.$store.getters.getUser.id
          })
        .then((invoice) => {

          this.invoice = invoice
          resolve(invoice)

        }).catch((e)=>{
          reject(e)
        })
      })
    },
    previousStep() {
      if (this.activeStep > 0){
        this.activeStep--
      }
    },
    savePlan(){
      this.$refs.planForm.submit().then((plan)=>{
        this.$store.dispatch('setNewPlan', plan)
        this.plan = plan
        this.task.plan = plan.id

        this.activeStep++
      })
    },
    saveInputMethod(){
      this.inputLoading = true
      if (this.inputType == "album"){
        this.$refs.albumForm.submit().then(async (album)=>{
          this.$store.dispatch('setNewAlbum', album)
          this.album = album
          this.plan.album = album
          this.task.album = album.id

          this.$store.dispatch('setNewPlan', this.plan)

          //update plan with album
          await this.$store.dispatch('editPlan', this.plan)

          //create task
          await this.createTask().then((task)=>{
            this.task = task
            this.$store.dispatch('setNewTask', task)
          })

          await this.sleep(2000)

          await this.createInvoice(this.task).then((invoice)=>{
            this.invoice = invoice
            this.$refs.billingView.setInvoice(invoice)
            this.activeStep++
          })


        }).catch(()=>{
          this.inputLoading = false
        })
      }else if(this.inputType == "orthomosaic") {
        this.inputLoading = true
        this.$refs.orthomosaicForm.submit().then(async (orthomosaic)=>{
          this.$store.dispatch('setNewOrthomosaic', orthomosaic)
          if (this.plan.orthomosaic == undefined){
            this.plan.orthomosaic = []
          }
          this.plan.orthomosaic.push(orthomosaic)
          this.task.orthomosaic = orthomosaic.id

          //update plan with album
          await this.$store.dispatch('editPlan', this.plan)

          //create task
          await this.createTask().then((task)=>{
            this.task = task
            this.$store.dispatch('setNewTask', task)
          })

          await this.sleep(2000)

          //create invoice passing task
          await this.createInvoice(this.task).then((invoice)=> {
            this.invoice = invoice
            this.$refs.billingView.setInvoice(invoice)
            this.activeStep++
          })

        }).catch(()=>{
          this.inputLoading = false
        })
      }
    },
    finishNewPlan () {
      this.$router.push({ path: '/app/plan/'+this.plan.id, params:{ planId: this.plan.id } }) 
    }
  }
};
</script>

