<template>
  <div class="new-project">

    <navigation-menu title="Novo Projeto"></navigation-menu>

    <b-steps size="is-small" v-model="activeStep" :animated="true" :has-navigation="false">
      <b-step-item type="is-success" icon-pack="fas" icon="map-marker" :clickable="false">
        <project-form :showButton="false" ref="projectForm"></project-form>
        <b-field>
          <b-button
            class="button next-bt is-primary is-pulled-right"
            @click="saveProject"
          >Próximo</b-button>
        </b-field>
      </b-step-item>

      <b-step-item type="is-success" icon-pack="fas" icon="map" :clickable="false">

        <b-field label="Mapas">
          <b-select v-model="currentPlan" placeholder="Selecione" expanded>
            <option
              v-for="plan in filteredPlans"
              :value="plan.id"
              :key="plan.id">
              {{ plan.name }}
            </option>
          </b-select>
        </b-field>
        <div class="columns">
          <div class="column has-text-centered">
            <b-button class="selectPlan is-primary" @click="addPlan()">
              <i class="fas fa-plus-circle"></i>
              Selecionar
            </b-button>
          </div>
          <div class="column has-text-centered">
            <b-button class="selectPlan is-primary" @click="createPlan()">
              <i class="fas fa-plus-circle"></i>
              Novo Mapa
            </b-button>
          </div>
        </div>

        <div class="field">
          <b-switch :value="newPlan" v-model="newPlan"
          type="is-success">
              Criar novo mapa
          </b-switch>
        </div>

        <b-field label="Mapas selecionados:" v-if="project.plans && project.plans.length">
          <ul class="selectedMaps">
            <li class="selectedMap"
              v-for="plan in project.plans"
                :value="plan.id"
                :key="plan.id">
                {{ plan.name }}

              <div class="remove" @click="removePlan(plan.id)">
                <i class="fas fa-times"></i>
              </div>
            </li>
          </ul>
        </b-field>

        <b-field>
          <b-button
            class="button next-bt is-primary is-pulled-right"
            @click="activeStep++"
            v-if="newPlan"
          >Próximo</b-button>
          <b-button
            class="button next-bt is-primary is-pulled-right"
            @click="finishNewProject"
            v-if="!newPlan"
          >Finalizar</b-button>
        </b-field>
      </b-step-item>

      <b-step-item type="is-success" icon-pack="fas" icon="plus-circle" :clickable="false" v-if="newPlan">

        <plan-form :showButton="false" ref="planForm"></plan-form>

        <b-field>
          <b-button
            class="button next-bt is-primary is-pulled-left"
            @click="cancelPlan"
          >Cancelar</b-button>
        </b-field>
          <b-button
            class="button next-bt is-primary is-pulled-right"
            @click="savePlan"
          >Próximo</b-button>
        </b-field>
      </b-step-item>

      <b-step-item type="is-success" icon-pack="fas" icon="images" :clickable="false" v-if="newPlan">
        <b-field label="Tipo de Imagem" :type="{'is-danger': errors.has('ProcessType')}" :message="errors.first('ProcessType')">

        </b-field>
        <b-field v-if="showInputSelector">
          <b-radio v-model="inputType"
            size="is-small"
            type="is-success"
            :disabled="processing"
            native-value="album">
            Imagens de drone
          </b-radio>
          
          <b-radio v-model="inputType"
            size="is-small"
            :disabled="processing"
            type="is-success"
            native-value="orthomosaic">
            Ortomosaico
          </b-radio>
        </b-field>

        <album-form v-if="inputType == 'album'" :showButton="false" ref="albumForm"></album-form>
        <orthomosaic-form v-if="inputType == 'orthomosaic'" :showButton="false" ref="orthomosaicForm"></orthomosaic-form>


        <b-field>
          <b-button
            class="button prev-bt is-primary is-pulled-left"
            @click="previousStep"
          >Anterior</b-button>
        </b-field>
        <b-field>
          <b-button
            class="button next-bt is-primary is-pulled-right"
            @click="saveInputMethod"
            :loading="inputLoading"
            :disabled="inputLoading"
          >Próximo</b-button>
        </b-field>

      </b-step-item>

      <b-step-item type="is-success" icon-pack="fas" icon="credit-card"  :clickable="false" v-if="newPlan">
        <!-- Seu upload está em andamento e pode ser visualizado no botão amarelo da barra lateral. Por favor, feche ou não atualize a página.  -->
        <!-- Enquanto isso, você pode navegar livremente pela plataforma. -->
        <billing-view
        :album="album"
        :plan="plan"
        :task="task"
        :invoice="invoice"
        ref="billingView"
        :showButton="false">
        </billing-view>

        <b-field>
          <b-button
            class="button next-bt is-primary is-pulled-right"
            @click="finishNewProject"
          >Finalizar</b-button>
        </b-field>
      </b-step-item>
    </b-steps>

  </div>
</template>

<script>
import NavigationMenu from "../NavigationMenu";
import ProjectForm from '@/components/app/project/ProjectForm'
import PlanForm from '@/components/app/plan/PlanForm'
import AlbumForm from '@/components/app/album/AlbumForm'
import OrthomosaicForm from '@/components/app/orthomosaic/OrthomosaicForm'
import BillingView from '@/components/app/billing/BillingView'

export default {
  name: "new-project",
  components: {
    NavigationMenu,
    ProjectForm,
    PlanForm,
    AlbumForm,
    OrthomosaicForm,
    BillingView
  },
  data() {
    return {
      inputType: "",
      listPlans: [],
      currentPlan: {},
      newPlan: false,
      inputLoading: false,
      showInputSelector: true,
      processing: false,
      activeStep: 0,
      btDisabled: false,
      item: "",
      project: {
        plans: []
      },
      plan: {
        id: '',
        name: '',
        location: '',
        latlng: null,
        album: '',
        owner: this.$store.state.auth.user.id,
        project: null,
      },
      album: null,
      task: {
        id: null,
        job: {orthomosaic: true},
        status: "waiting"
      },
      invoice: null,
    };
  },
  computed: {
    filteredPlans() {
      if (this.project.plans){
        let self = this

        return this.listPlans.filter(function (el) {
          return !self.project.plans.some(function (f) {
            return f._id === el._id;
          });
        });

      }else{
        return this.listPlans
      }
    }
  },
  mounted() {
    this.$store.dispatch('getPlans', "owner="+this.$store.getters.getUser.id)
    .then((plans) => {
      this.listPlans = plans
    })
  },
  methods: {
    sleep (m) {return new Promise(r => setTimeout(r, m))},
    createTask: function() {
      return new Promise((resolve, reject) => {
        this.$store.dispatch('postTask', this.task)
        .then((task) => {
          this.task = task
          resolve(task)
        }).catch((e)=>{
          reject(e)
        })
      })
    },
    createInvoice: function(task) {
      return new Promise((resolve, reject) => {

        this.$store.dispatch('postInvoice', {
          task:task,
          owner: this.$store.getters.getUser.id
          })
        .then((invoice) => {

          this.invoice = invoice
          resolve(invoice)

        }).catch((e)=>{
          reject(e)
        })
      })
    },
    previousStep() {
      if (this.activeStep > 0){
        this.activeStep--
      }
    },
    saveProject(){
      this.$refs.projectForm.submit().then((project)=>{
        this.$store.dispatch('setNewProject', project)
        this.project = project
        this.task.project = project.id

        this.activeStep++
      })
    },

    async addPlan () {
      var index = this.listPlans.findIndex(plan => plan.id === this.currentPlan)
      this.project.plans.push(this.listPlans[index])

      await this.$store.dispatch('editProject', this.project)

      this.currentPlan = {}
      // this.listPlans.splice(index, 1)
    },

    async removePlan (planId) {
      var index = this.project.plans.findIndex(plan => plan.id === planId)
      this.project.plans.splice(index, 1)

      await this.$store.dispatch('editProject', this.project)
    },

    createPlan(){
      this.newPlan = true
      this.$store.dispatch('setNewProject', this.project).then((project)=>{
        this.activeStep++
      })
    },
    
    savePlan(){
      this.$refs.planForm.submit().then(async (plan)=>{
        this.$store.dispatch('setNewPlan', plan)
        this.plan = plan
        this.task.plan = plan.id

        this.project.plans.push(plan)
        await this.$store.dispatch('editProject', this.project)

        this.activeStep++
      })
    },

    async cancelPlan(){
      if (this.plan.id){
        var index = this.project.plans.findIndex(plan => plan.id === this.plan._id)
        this.project.plans.splice(index, 1)
  
        await this.$store.dispatch('editProject', this.project)
  
        this.$store.dispatch('setNewPlan', null)
        this.task.plan = null
  
        this.$store.dispatch('deletePlan', this.plan._id)
        .then( () => {
          this.$notify({
            group: 'global',
            type: 'is-success',
            duration: 2000,
            title: 'Mapa removido'
          });
        })
      }
      this.activeStep--

    },

    saveInputMethod(){
      this.inputLoading = true
      if (this.inputType == "album"){
        this.$refs.albumForm.submit().then(async (album)=>{
          this.$store.dispatch('setNewAlbum', album)
          this.album = album
          this.plan.album = album
          this.task.album = album.id

          this.$store.dispatch('setNewPlan', this.plan)

          //update plan with album
          await this.$store.dispatch('editPlan', this.plan)

          //create task
          await this.createTask().then((task)=>{
            this.task = task
            this.$store.dispatch('setNewTask', task)
          })

          await this.sleep(2000)

          await this.createInvoice(this.task).then((invoice)=>{
            this.invoice = invoice
            this.$refs.billingView.setInvoice(invoice)
            this.activeStep++
          })

        }).catch(()=>{
          this.inputLoading = false
        })
      }else if(this.inputType == "orthomosaic") {
        this.inputLoading = true
        this.$refs.orthomosaicForm.submit().then(async (orthomosaic)=>{
          this.$store.dispatch('setNewOrthomosaic', orthomosaic)
          if (this.plan.orthomosaics == undefined){
            this.plan.orthomosaics = []
          }
          this.plan.orthomosaics.push(orthomosaic)
          this.task.orthomosaic = orthomosaic.id

          //update plan with album
          await this.$store.dispatch('editPlan', this.plan)

          //create task
          await this.createTask().then((task)=>{
            this.task = task
            this.$store.dispatch('setNewTask', task)
          })

          await this.sleep(2000)

          //create invoice passing task
          await this.createInvoice(this.task).then((invoice)=> {
            this.invoice = invoice
            this.$refs.billingView.setInvoice(invoice)
            this.activeStep++
          })

        }).catch(()=>{
          this.inputLoading = false
        })
      }
    },
    finishNewProject () {
      this.$router.push({ path: '/app/project/'+this.project.id, params:{ projectId: this.project.id } }) 
    }
  }
};
</script>

<style lang="scss">
  .new-project {
    overflow-y: auto;
    overflow-x: hidden;
    width: 100%;
    padding: 10px;

    &::-webkit-scrollbar-track{
      background-color: #F5F5F5;
    }

    &::-webkit-scrollbar{
      width: 8px;
      background-color: #F5F5F5;
    }

    &::-webkit-scrollbar-thumb{
      // border-radius: 10px;
      background-color: #888;
    }

    .plan-form{
      margin-bottom: 20px;
    }

    .createPlan{
      cursor: pointer;
    }

    .selectPlan{
      cursor: pointer;
    }

    .step-items{
      width: 100%;
    }

    .selectedMaps{

      .selectedMap{
        position: relative;
        padding: 5px;
        background-color: $color-lighter;
        color: $color-dark;
        margin-bottom: 5px;

        .remove{
          color: red;
          cursor: pointer;
          @include v-centerer;
          right: 10px;
        }
      }
    }


    .b-steps{

      .steps:not(:last-child){
        margin-bottom: 5px;
        margin-top: 10px;
      }

      .steps{
        // display: none;

        + .step-content{
          padding: 0;
        }
      } 
    }


    .content {
      // padding: 10px;

      .sectionTitle {
        padding: 3px 0px 3px 0;

        h2 {
          font-size: 20px;
          font-weight: bold;
          line-height: inherit;
        }
      }

      .button{
        margin-top: 20px;
        background-color: $color-light;
      }
    }
  }
</style>